<template>
  <div class="page-container">
    <div class="page-box accent">
      <div class="page-box-title">
        <h2>Klanten</h2>
        <div class="flex-divider"></div>
        <base-button @click="openCreateModal" class="small-on-mobile">
          <base-icon iconRef="plus" position="left" />
          <span class="button-label">Nieuwe Klant</span>
        </base-button>
      </div>
      <div class="page-box-datatable-container">
        <ais-instant-search :search-client="searchClient" index-name="prod_CLIENTS">
          <ais-toggle-refinement
            attribute="salesOwner"
            label="Toon alleen eigen klanten"
            :on="userFullName"
          />
          <ais-toggle-refinement
            v-show="userIsAdmin"
            attribute="trashed"
            label="Verwijderd"
            :on="true"
            :off="false"
          />
          <ais-search-box />
          <ais-hits>
            <button
              slot="item"
              slot-scope="{ item }"
              class="client-list-item"
              @click="navigateToClient(item.objectID)"
            >
              <div class="client-list-item--names">
                <div
                  class="client-list-item--client-name"
                >{{ item.clientName }}</div>
                <div class="client-list-item--contact-name">
                  {{
                  item.contactFirstName || item.contactLastName
                  ? item.contactFirstName + " " + item.contactLastName
                  : "-"
                  }}
                </div>
              </div>
              <div class="client-list-item--date">
                {{
                  item.createdAt ? new Date(item.createdAt._seconds * 1000).toLocaleDateString(
                    "nl-NL",
                    {
                      day: "numeric",
                      month: "long",
                      year: "numeric",
                    }
                  ) || "" : "-"
                }}
              </div>
              <div
                class="client-list-item--sales-owner"
              >{{ item.salesOwner ? item.salesOwner : "-" }}</div>
            </button>
          </ais-hits>
          <ais-pagination />
        </ais-instant-search>
      </div>
    </div>
  </div>
</template>

<style lang="scss">
.ais-SearchBox {
  margin-top: 8px;
  padding: 16px 24px;
}

.ais-SearchBox-form {
  position: relative;
}

.ais-SearchBox-input {
  width: 100%;
  padding: 12px 12px 12px 48px;
  border: 1px solid #999;
  border-radius: 4px;
  &:focus {
    outline: none;
    border-color: #009ad9;
  }
}

.ais-SearchBox-submit {
  position: absolute;
  top: 0;
  left: 16px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  pointer-events: none;
}

.ais-SearchBox-submitIcon {
  height: 16px;
  width: 16px;
}

.ais-SearchBox-reset {
  position: absolute;
  top: 0;
  right: 16px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
}

.ais-SearchBox-resetIcon {
  height: 12px;
  width: 12px;
}

.client-list-item {
  display: grid;
  grid-template-columns: 3fr 1fr 1fr;
  align-items: center;
  width: 100%;
  padding: 8px 24px;
  text-align: left;
  border: none;
  background: #fff;
  border-top: 1px solid #e9e9e9;
  cursor: pointer;
  font-size: 16px;
  &:hover {
    background: #eefaff;
  }
}

.client-list-item--names {
  display: flex;
  flex-direction: column;
}

.client-list-item--client-name {
  font-weight: bold;
  color: #009ad9;
}

.client-list-item--contact-name {
  font-weight: 600;
  margin-top: 4px;
  color: #595959;
}

.client-list-item--date {
  font-weight: 600;
  color: #595959;
}

.client-list-item--sales-owner {
  font-weight: 600;
  color: #595959;
}

.ais-Pagination {
  border-top: 1px solid #eee;
}

.ais-Pagination-list {
  display: flex;
  width: 100%;
  justify-content: center;
}

.ais-Pagination-item:not(:last-child) .ais-Pagination-link {
  border-right: 1px solid #eee;
}

.ais-Pagination-link {
  height: 48px;
  width: 48px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.ais-ToggleRefinement {
  margin-top: 8px;
  margin-left: 24px;
  user-select: none;
  display: inline-block;
}

.ais-ToggleRefinement-label {
  padding: 8px 12px;
  border-radius: 4px;
  border: 1px solid #999;
  cursor: pointer;
}

.ais-ToggleRefinement-labelText {
  margin-left: 4px;
}

.ais-ToggleRefinement-count {
  display: none;
}
</style>

<script>
import algoliasearch from "algoliasearch/lite";

export default {
  name: "Clients",
  data() {
    return {
      searchClient: algoliasearch(
        "B9KCQKP8E1",
        "0382472d88154c2158abc0dc678ade85"
      )
    };
  },
  computed: {
    userFullName() {
      const userData = this.$store.getters.getUsersList.find(user => {
        return user.data().uid == this.$store.getters.getUser.uid;
      });
      return `${userData.data().firstName} ${userData.data().lastName}`;
    },
    userIsAdmin() {
      return this.$store.getters.getCurrentUserAdminStatus;
    }
  },
  methods: {
    openCreateModal() {
      this.$store.commit("setModalShown", {
        dataType: "client",
        modalType: "create",
        shown: true
      });
    },
    dataEntryClick(value) {
      this.openClient(value);
    },
    navigateToClient(targetClientId) {
      this.$router.push(`/klant/${targetClientId}`);
    }
  },
  created() {
    this.$setPageTitle("Klanten");
  }
};
</script>



